import React, { useState } from "react";
import { firebaseAuth } from "../Fbase";
import { message } from "antd";

const AuthForm = () => {
  const [Email, setEmail] = useState("");
  const [Password, setPassword] = useState("");
  const [PasswordCheck, setPasswordCheck] = useState("");
  const [NewAccount, setNewAccount] = useState(true);
  const [ErrorMessage, setErrorMessage] = useState("");

  const onSubmit = async (event) => {
    event.preventDefault();
    if (NewAccount && Password !== PasswordCheck) {
      setErrorMessage("ë¹„ë°€ë²ˆí˜¸ê°€ ì¼ì¹˜í•˜ì§€ ì•ŠìŠµë‹ˆë‹¤.");
      return;
    }
    try {
      if (NewAccount) {
        await firebaseAuth.createUserWithEmailAndPassword(Email, Password);
        return message.success("ê³„ì •ì´ ì„±ê³µì ìœ¼ë¡œ ìƒì„±ë˜ì—ˆìŠµë‹ˆë‹¤.");
      } else {
        await firebaseAuth.signInWithEmailAndPassword(Email, Password);
        return message.success("Welcome to Carrot Field");
      }
    } catch (error) {
      console.log("login form submit error :: ", error.message);
      setErrorMessage(error.message);
    }
  };

  const onChange = (event) => {
    const { name, value } = event.target;

    if (name === "email") {
      setEmail(value);
    } else if (name === "password") {
      setPassword(value);
    } else if (name === "password-check") {
      setPasswordCheck(value);
    }
  };

  const toggleAccount = () => {
    setNewAccount((prev) => !prev);
  };
  return (
    <>
      <form onSubmit={onSubmit} className="container">
        <input
          type="email"
          placeholder="E-mail"
          name="email"
          onChange={onChange}
          value={Email}
          required
          className="authInput"
        />
        <input
          type="password"
          placeholder="Password"
          name="password"
          onChange={onChange}
          value={Password}
          required
          className="authInput"
        />
        {NewAccount && (
          <input
            type="password"
            placeholder="Password Check"
            name="password-check"
            onChange={onChange}
            value={PasswordCheck}
            required
            className="authInput"
          />
        )}
        <input
          type="submit"
          value={NewAccount ? "ê³„ì • ìƒì„±" : "ë¡œê·¸ì¸"}
          className="authInput authSubmit"
        />
        {ErrorMessage && <span className="authError">{ErrorMessage}</span>}
      </form>
      <span onClick={toggleAccount} className="authSwitch">
        {NewAccount ? "ë¡œê·¸ì¸ í•˜ê¸°" : "ê³„ì • ìƒì„±í•˜ê¸°"}
      </span>
    </>
  );
};

export default AuthForm;
